main {
    max-height: 100vh;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
    align-items: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    transition: var(--transition, 1s);
}
div.cardsList {
    width: 70%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    background: rgba(218, 218, 218, .7);
    margin-bottom: 2rem;
    padding: 2rem;
    font-family: AllodsWest;
    cursor: default;
}
div.answersList {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
    align-items: stretch;
    gap: var(--size-great, 1rem);
}
.answersList > button {
    min-width: 100px;
    background: url("../../FolderFrame.png") no-repeat scroll center/100% 100%;
    padding: .5rem 2rem;
    border: none;
    /*border-radius: var(--size-hard, .5rem);*/
    outline: none;
    font-family: AllodsWest;
    color: #dadada;
    cursor: pointer;
}
.answersList > button:hover {
    background: url("../../FolderFrameSelected.png") no-repeat scroll center/100% 100%;
}